@import "./ResponsiveVal";



@include mobile {
  .responsive-background{
    height: 800px;
  }
  .responsive-category{
    margin: 30px;
  }
  .responsive-categories{
    margin: 0x;
    top: 300px;
  }
}

@include tablet {
  .responsive-background{
    height: 900px;
  }
  .responsive-category{
    margin: 0 50px;
  }
  .responsive-categories{
    margin: 0 80px;
  }
}

@include desktop {
  .responsive-background{
    height: 1000px;
  }
  .responsive-category{
    margin: 0 50px;
  }
  .responsive-categories{
    margin: 0 340px;
    top: 400px;
  }
}

.background {
  @extend .responsive-background;
  background-image: url('./background.jpg');
  background-size: cover;
  background-color: white;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
}

.category {
  @extend .responsive-category;
    background-color: black;
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 300px;
    flex-direction: column;
    width: auto;
    height: 300px;
  }

  .categories {
    @extend .responsive-categories;
    display: flex;
    align-items: center;
    position: relative;
    overflow-x: scroll;
    overflow-y: hidden;
  }

  .categoryTitle {
    color: white;
    font-variant: inherit;
  }

  .categoryContent {
    color: white;
  }

  .copyright_background {
    background-color: black;
    width: 100%;
    height: 150px;
  }