
// BreakPoint
$breakpoing-mobile: 335px;
$breakpoing-tablet: 750px;
$breakpoing-desktop: 1024px;

@mixin mobile {
    @media (min-width: #{$breakpoing-mobile}) and (max-width: #{$breakpoing-tablet - 1px}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$breakpoing-tablet}) and (max-width: #{$breakpoing-desktop - 1px}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{$breakpoing-desktop}) {
        @content;
    }
}
